<template>
  <div class="base-info common-info">
    <div class="topTitle">
      <span class="topTitle-span1">客户视图</span>
      <span class="topTitle-span1">
        <img src="@/assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">基本信息</span>
    </div>
    <div class="contentCellBox" style="padding-bottom: 20px;">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>用户信息
        </div>
        <div class="tab-desc">
          <span>{{data.CLIENT_ID}}{{data.CLIENT_NAME}}</span>
        </div>
        <table class="first-tab" cellspacing="0" cellpadding="0">
          <tr>
            <td>分支机构号</td>
            <td>{{data.BRANCH_NO}}</td>
            <td>客户状态</td>
            <td>{{data.CLIENT_STATUS}}</td>
          </tr>
          <tr>
            <td>国籍</td>
            <td>{{data.NATIONALITY}}</td>
            <td>证件类型</td>
            <td>{{data.ID_KIND}}</td>
          </tr>
          <tr>
            <td>证件号码</td>
            <td>{{data.ID_NO}}</td>
            <td>委托方式</td>
            <td>{{data.ENTRUST_WAY}}</td>
          </tr>
          <tr>
            <td>客户权限</td>
            <td>{{data.CLIENT_RIGHTS}}</td>
            <td>客户限制</td>
            <td>{{data.RESTRICTION}}</td>
          </tr>
          <tr>
            <td>用户性别</td>
            <td>{{data.CLIENT_SEX}}</td>
            <td>证件有效期起始日期</td>
            <td>{{data.ID_BEGINDATE}}</td>
          </tr>
          <tr>
            <td>销户日期</td>
            <td>{{data.CANCEL_DATE}}</td>
            <td>是否机构户标志</td>
            <td>{{data.ORGAN_FLAG}}</td>
          </tr>
          <tr>
            <td>开户日期</td>
            <td>{{data.OPEN_DATE}}</td>
            <td>邮政编码</td>
            <td>{{data.ZIPCODE}}</td>
          </tr>
          <tr>
            <td>地址</td>
            <td>{{data.ADDRESS}}</td>
            <td>证件地址</td>
            <td>{{data.ID_ADDRESS}}</td>
          </tr>
          <tr>
            <td>电话号码</td>
            <td>{{data.PHONECODE}}</td>
            <td>传真</td>
            <td>{{data.FAX}}</td>
          </tr>
          <tr>
            <td>手机号码</td>
            <td>{{data.MOBILETELEPHONE}}</td>
            <td>邮箱</td>
            <td>{{data.E_MAIL}}</td>
          </tr>
          <tr>
            <td>备注</td>
            <td>{{data.REMARK}}</td>
            <td>行业代码</td>
            <td>{{data.PROFESSION_CODE}}</td>
          </tr>
          <tr>
            <td>证件有效期到期日期</td>
            <td>{{data.ID_ENDDATE}}</td>
            <td>学历</td>
            <td>{{data.DEGREE_CODE}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    let obj = this.$route.query;
    if (obj.CLIENT_ID){
      this.$store.commit("clientId", {clientId: obj.CLIENT_ID});
      this.storage.set("clientId", obj.CLIENT_ID);
      this.$store.commit("type", {type: obj.type});
      this.storage.set("type", obj.type);
    }else {
      obj.CLIENT_ID = this.$store.state.clientId;
      obj.type = this.$store.state.type;
    }
    this.axios
    .get(`/client/patch/list/info?clientId=${obj.CLIENT_ID}&clientType=${obj.type}`)
    .then(response => {
        if (response.code == "200") {
            this.data = response.data[0];
        }
    })
    .catch(error => {});
  },
  data() {
    return {
      data: {},
    };
  },
  methods: {
  },
  watch: {}
};
</script>
<style lang="scss">
</style>


